input,
select,
textarea,
input.form-control,
select.form-control,
textarea.form-control {
  border-radius: 0px;
  border: 1px solid transparent;
  color: #333;
  text-align: center;
  background-color: #f8f8f8;
}
input:focus,
select:focus,
textarea:focus,
input.form-control:focus,
select.form-control:focus,
textarea.form-control:focus {
  border: 2px solid #06c47a;
}
input[disabled],
select[disabled],
textarea[disabled],
input.form-control[disabled],
select.form-control[disabled],
textarea.form-control[disabled] {
  border: 2px solid #a9a9a9;
}
input.is-error,
select.is-error,
textarea.is-error {
  border: 2px solid #de4040;
  color: #333;
}
input.is-valid,
select.is-valid,
textarea.is-valid {
  border: 2px solid;
  color: #333;
}
input[type="file"] {
  border: 0;
}
select,
select.form-control {
/* styling */
  display: inline-block;
  line-height: 1.5em;
  padding: 0.5em 3.5em 0.5em 1em;
/* reset */
  margin: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
}
.input-lg-register {
  font-size: 14px;
  height: 53px;
}
select,
select.form-control {
  background-image: url("http://www.pngmart.com/files/3/Down-Arrow-PNG-Clipart.png");
  background-position: calc(100% - 20px) 1em;
  background-size: 15px;
  background-repeat: no-repeat;
}
button {
  border-radius: 0;
  border: 0;
}
.btn {
  border-radius: 3px;
  padding: 10px 40px;
}
.btn border 1px solid $colors['oceano'],
.btn color: $colors['oceano'],
.btn background-color:$btn-bg,
.btn transition color 1s,
.btn box-shadow 1s,
.btn:hover {
  background-color: transparent;
  color: #06c47a;
  box-shadow: 2px 0px 8px #9e9c9c;
}
.btn:focus {
  background-color: #06c47a;
  color: #06c47a;
}
.btn:active {
  background-color: transparent;
  color: #06c47a;
}
.btn-default {
  background-color: transparent;
  color: #06c47a;
  border: 1px solid #06c47a;
}
.btn-default:hover {
  background-color: transparent;
  color: #06c47a;
  border: 1px solid #06c47a;
}
.btn-danger,
.btn-success,
.btn-warning {
  border-radius: 3px;
  padding: 10px 40px;
  color: #06c47a;
}
.btn-danger {
  background-color: #c10500;
}
.btn-danger:hover {
  background-color: #cc0500;
}
.btn-success {
  background-color: #5cb85c;
}
.btn-success:hover {
  background-color: #3e8e3e;
}
.btn-warning {
  background-color: #f0ad4e;
}
.btn-warning:hover {
  background-color: #bc7510;
}
