header {
  height: auto;
}
#topo {
  background-color: rgba(0,0,0,0.8);
  z-index: 9999999;
  width: 100%;
  top: 0;
}
.logo-container {
  padding: 15px 0;
}
#topo.redu {
  background-color: rgba(0,0,0,0.95);
}
.line {
  position: relative;
}
.line::after {
  content: " ";
  width: 30px;
  height: 1px;
  background-color: #000;
  position: absolute;
  bottom: 0;
  left: 0;
}
.cursos-todos h4 {
  font-size: 16px;
}
.cursos-todos .curso {
  border: 1px solid #ebebeb;
  margin-top: 20px;
}
.div-curso-back {
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  position: relative;
  background-position: center center;
}
.div-curso-back::after {
  content: "";
  display: block;
  padding-bottom: 90%;
}
.div-curso-back a {
  opacity: 0;
  display: flex;
  justify-content: center;
  align-self: center;
  position: absolute;
  color: #fff;
  text-decoration: none;
  width: 100%;
  height: 100%;
  transition: 1s;
  background-color: rgba(6,196,122,0.8);
}
.div-curso-back a > * {
  align-self: center;
}
.div-curso-back:hover a {
  opacity: 1;
}
.artigo-data {
  margin: 0 auto;
  padding-left: 20px;
}
.artigo-w {
  background-color: #fff;
  transition: 1s;
  color: #000;
  padding: 0 20px;
  padding-top: 30px;
  padding-bottom: 30px;
  border-bottom: 1px solid #d3d3d3;
}
.artigo-w:first-child {
  margin-top: 30px;
}
.artigo-w:not(:last-child) {
  margin-bottom: 30px;
}
.artigo-w .dia {
  font-size: 65px;
}
.artigo-w:hover {
  background-color: #333;
}
.artigo-w:hover .artigo-textos h4 {
  color: #06c47a;
}
.artigo-w:hover .artigo-textos p {
  color: #fff;
}
.arrowsPlace {
  width: 120px;
  height: 40px;
  position: relative;
  right: 0;
  float: right;
}
.section {
  padding: 40px 0;
}
.cursos-todos {
  padding: 30px 5px;
}
.line-data {
  width: 100%;
  height: 2px;
  background-color: #000;
}
.line-data .block {
  width: 30px;
  height: 4px;
  margin: auto;
  margin-top: -1px;
  background-color: #06c47a;
}
.line-regiter {
  width: 40px;
  height: 1px;
  background-color: #000;
  margin: auto;
}
#registrar {
  padding: 120px 0;
}
.register-texto {
  margin-top: 80px;
}
.form-index {
  width: 100%;
  margin: auto;
  max-width: 450px;
  padding: 5px 20px;
  padding-bottom: 40px;
  background-color: #fff;
}
.form-index .form-group {
  max-width: 380px;
  margin: auto;
}
.form-index button.bg-oceano {
  color: #fff;
  font-size: 13px;
}
.form-index button.input-lg {
  border-radius: 0;
}
.form-index > input {
  margin-top: 20px;
}
img.slick-disabled {
  filter: grayscale(100%);
}
.slick-next,
.slick-prev {
  z-index: 99999;
}
.slick-prev {
  left: 25px;
}
.slick-next {
  right: 25px;
}
.seta-right,
.seta-left {
  position: absolute;
  top: 50%;
  z-index: 999;
  cursor: pointer;
}
.seta-right {
  right: 25px;
}
.seta-left {
  left: 25px;
}
.icon-data {
  background-image: url("../img/icone.jpg");
  background-position: 15px center;
  background-repeat: no-repeat;
  margin-top: 20px;
}
.artigo-textos {
  padding-left: 80px;
}
.artigo-textos h4 {
  text-align: left;
}
.artigo-textos p {
  text-align: left;
}
@media screen and (max-width: 768px) {
  #registrar {
    padding: 60px 0;
  }
  .register-texto {
    margin-top: 40px;
  }
  .artigo-w .dia {
    font-size: 45px;
  }
  .artigo-textos {
    padding-left: 20px;
  }
  .artigo-textos h4 {
    text-align: left;
  }
  .artigo-textos p {
    text-align: left;
  }
  .line {
    position: relative;
  }
  .line::after {
    display: none;
  }
}
footer {
  padding: 120px 0 30px 0px;
}
footer .tel {
  font-weight: 100;
  font-size: 30px;
}
footer .copyright {
  text-align: left;
}
footer .copyright img {
  margin: 0;
}
footer .rede-sociais {
  text-align: center;
}
footer .rede-sociais a:nth-child(2) {
  margin: 0 10px;
}
footer .contato {
  text-align: right;
}
@media screen and (max-width: 768px) {
  footer {
    padding: 60px 0 30px 0px;
  }
  footer .copyright {
    text-align: center;
  }
  footer .copyright img {
    margin: 0 auto;
  }
  footer .rede-sociais {
    text-align: center;
    margin: 30px 0;
  }
  footer .contato {
    text-align: center;
  }
}
@media screen and (max-width: 576px) {
  footer {
    padding: 60px 0 30px 0px;
  }
  footer .copyright,
  footer .rede-sociais,
  footer .contato,
  footer p {
    font-size: 12px;
  }
}
.navegacao {
  border-top: 1px solid #06c47a;
}
.menu {
  cursor: pointer;
}
#desk-nav {
  color: #fff;
  display: flex;
  justify-content: space-between;
  padding: 0;
  margin: 0;
}
#desk-nav li {
  list-style: none;
  font-weight: 100;
  font-size: 12px;
}
#desk-nav li .nav-item {
  display: block;
  padding: 20px 10px;
  color: #fff;
  letter-spacing: 0.3;
  text-decoration: none;
  background-color: transparent;
  transition: 1s background-color, 1s box-shadow;
  box-shadow: 0px 0px 10 px none;
}
#desk-nav li .nav-item:hover {
  background-color: #06c47a;
  box-shadow: 0px 0px 2px #06c47a;
}
#desk-nav li .nav-item.ativo {
  color: #06c47a;
}
@media (max-width: 1200px) {
  #desk-nav {
    display: none;
    flex-direction: column;
  }
  .menu {
    display: none;
  }
}
@supports (-ms-ime-align: auto) {
  .div-curso-back {
    height: 250px;
  }
}
